export function workExperience(type) {
  switch (type) {
    case 1:
      return "مهم نیست";
      break;

    case 2:
      return "کمتر از 3 سال";
      break;

    case 3:
      return "بین 3 تا 7 سال";
      break;

    case 4:
      return "بیشتر از 7 سال";
      break;
  }
}
